import { AxiosInstance } from 'axios';
import { AxiosParam, create, send } from 'summer';
import { ConsultaMovimentacaoSaldo } from '../models/dtos/mcg/consultaMovimentacaoSaldo';
import { EvolucaoContaContabilGrafReturn } from '../models/dtos/mcg/evolucaoContaContabilGrafReturn';
import { SaldoCriteria } from '../models/dtos/mcg/saldoCriteria';

function getService(): AxiosInstance {
  return create('/mcg/dashboard', false);
}

export function consultarMovimentacaoWithSaldos(criteria: SaldoCriteria, param: AxiosParam<ConsultaMovimentacaoSaldo>) {
  send(getService().post<ConsultaMovimentacaoSaldo>('/consultarMovimentacaoWithSaldos', criteria), param);
}

export function evolucaoContaContabilGraf(criteria: SaldoCriteria, param: AxiosParam<EvolucaoContaContabilGrafReturn>) {
  send(getService().post<EvolucaoContaContabilGrafReturn>('/evolucaoContaContabilGraf', criteria), param);
}
