import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { FC, useEffect, useState } from 'react';
import Chart from 'react-apexcharts';
import BlockUi from 'react-block-ui';
import { useForm } from 'react-hook-form';
import Skeleton from 'react-loading-skeleton';
import { useDispatch, useSelector } from 'react-redux';
import { ScaleLoader } from 'react-spinners';
import { Card, Col, Row } from 'reactstrap';
import {
  decimalMaskFormatter,
  ERROR,
  formatDate,
  formatSaldo,
  isEmpty,
  PageTitle,
  parseLastDay,
  parseRefToDate,
  showToast,
  WARNING,
  WrapperSimple,
} from 'summer';
import { PlanoConta } from '../../../../models/dtos/ger/planoConta';
import { ConsultaMovimentacaoSaldo } from '../../../../models/dtos/mcg/consultaMovimentacaoSaldo';
import { SaldoCriteria } from '../../../../models/dtos/mcg/saldoCriteria';
import { MES_ENUM_VALUES } from '../../../../models/enumerated/mlf/mesEnum';
import { consultarMovimentacaoWithSaldos, evolucaoContaContabilGraf } from '../../../../services/dashboard.service';
import { Reducers } from '../../../../store/ducks';
import { GlobalState, setCurrentState } from '../../../../store/ducks/global.duck';
import { AZURE_RADIANCE, CARIBBEAN_GREEN, MY_SIN } from '../../../../utilities/colors';
import PlanoContaIS from '../../../components/mcg/planoContaIS/PlanoContaIS';

const areaOptions = {
  yaxis: {
    labels: {
      formatter: value => {
        return value.toFixed(0);
      },
    },
  },
};

const lineOptions = {
  chart: {
    stacked: false,
    type: 'line',
  },
  dataLabels: {
    enabled: false,
  },
  legend: {
    horizontalAlign: 'left',
    offsetX: 40,
  },
  stroke: {
    curve: 'smooth',
  },
  xaxis: {
    categories: MES_ENUM_VALUES.map(it => it.label),
    title: {
      text: 'Meses',
    },
  },
  yaxis: [
    {
      axisBorder: {
        color: CARIBBEAN_GREEN,
        show: true,
      },
      axisTicks: {
        show: true,
      },
      labels: {
        style: {
          colors: CARIBBEAN_GREEN,
        },
      },
      seriesName: 'Crédito',
      title: {
        style: {
          color: CARIBBEAN_GREEN,
        },
        text: 'Crédito',
      },
    },
    {
      axisBorder: {
        color: AZURE_RADIANCE,
        show: true,
      },
      axisTicks: {
        show: true,
      },
      labels: {
        style: {
          colors: AZURE_RADIANCE,
        },
      },
      seriesName: 'Débito',
      title: {
        style: {
          color: AZURE_RADIANCE,
        },
        text: 'Débito',
      },
      tooltip: {
        enabled: true,
      },
    },
    {
      axisBorder: {
        color: MY_SIN,
        show: true,
      },
      axisTicks: {
        show: true,
      },
      labels: {
        style: {
          colors: MY_SIN,
        },
      },
      opposite: true,
      seriesName: 'Evolução',
      title: {
        style: {
          color: MY_SIN,
        },
        text: 'Evolução',
      },
    },
  ],
};

const initialState: ConsultaMovimentacaoSaldo = {
  mapLancamentosDia: {},
  saldoAnterior: 0,
  saldoAtual: 0,
  totalCredito: 0,
  totalDebito: 0,
};

const DashboardMCG: FC = () => {
  const { globalParameter } = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const formProps = useForm({ mode: 'onBlur' });
  const dispatch = useDispatch();
  const [saldoCriteria, setSaldoCriteria] = useState<SaldoCriteria>();
  const [blockingAreaGraph, setBlockingAreaGraph] = useState(false);
  const [blockingLineGraph, setBlockingLineGraph] = useState(false);
  const [saldo, setSaldo] = useState<ConsultaMovimentacaoSaldo>(initialState);
  const [areaGraph, setAreaGraph] = useState([]);
  const [evolucaoContabil, setEvolucaoContabil] = useState([]);

  useEffect(
    () => {
      dispatch(setCurrentState('MCG'));
      const { empresaMCG, empresa } = globalParameter;
      const criteria: SaldoCriteria = {
        consolidar: 1,
        dtAte: formatDate(parseLastDay(empresaMCG.refAtual)),
        dtDe: formatDate(parseRefToDate(empresaMCG.refAtual, 1)),
        idGerEmpresa: empresa.id,
        idGerGrupoEmpresaAtual: empresa.grupoEmpresa.id,
      };
      const planoContaDashboard = localStorage.getItem(`planoContaDashboard${empresa.id}`);
      if (isEmpty(planoContaDashboard)) {
        setSaldoCriteria(criteria);
        formProps.reset({ conta: null });
        setSaldo(initialState);
        setAreaGraph([]);
        setEvolucaoContabil([]);
      } else {
        const planoConta = JSON.parse(planoContaDashboard);
        criteria.conta = planoConta.conta;
        criteria.idGerPlanoConta = planoConta.id;
        formProps.reset({ conta: planoConta });
        setSaldoCriteria(criteria);
        findMovimentacao(criteria);
        findEvolucaoContaContabil(criteria);
      }
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [globalParameter.empresa]
  );

  function changeConta(selectedItem: PlanoConta): void {
    const localStorageKey = `planoContaDashboard${globalParameter.empresa.id}`;
    if (selectedItem) {
      if (selectedItem.analitica) {
        localStorage.setItem(localStorageKey, JSON.stringify(selectedItem));
        const criteria = {
          ...saldoCriteria,
          conta: selectedItem.conta,
          idGerPlanoConta: selectedItem.id,
        };
        setSaldoCriteria(criteria);
        findMovimentacao(criteria);
        findEvolucaoContaContabil(criteria);
      } else {
        showToast('A conta selecionada deve ser analítica', WARNING);
      }
    } else {
      localStorage.setItem(localStorageKey, '');
    }
  }

  function findMovimentacao(criteria) {
    setBlockingAreaGraph(true);
    consultarMovimentacaoWithSaldos(criteria, {
      errorFunction: mensagem => showToast(mensagem, ERROR),
      thenFunction: result => {
        setSaldo(result);
        const diasLancamento = Object.keys(result.mapLancamentosDia);
        const data = diasLancamento.map(it => {
          return {
            x: it,
            y: result.mapLancamentosDia[it],
          };
        });
        setAreaGraph([
          {
            data,
            name: 'Nº Lançamentos',
          },
        ]);
        setBlockingAreaGraph(false);
      },
    });
  }

  function findEvolucaoContaContabil(criteria) {
    setBlockingLineGraph(true);
    evolucaoContaContabilGraf(criteria, {
      errorFunction: mensagem => showToast(mensagem, ERROR),
      thenFunction: result => {
        const seriesDebito = {
          data: result.saldoCDPrimaria,
          name: 'Débito',
          type: 'column',
        };
        const seriesCredito = {
          data: result.saldoCCPrimaria,
          name: 'Crédito',
          type: 'column',
        };
        const seriesAcumulada = {
          data: result.saldoAcumuladoPrimaria,
          name: 'Evolução',
          type: 'line',
        };
        setEvolucaoContabil([seriesDebito, seriesCredito, seriesAcumulada]);
        setBlockingLineGraph(false);
      },
    });
  }

  return (
    <>
      <PageTitle pageTitleShadow={true} titleHeading="Módulo Contábil" />
      <Row>
        <Col>
          <h5>Movimentação de contas</h5>
        </Col>
      </Row>
      <Row>
        <Col>
          <PlanoContaIS id="conta" formProps={formProps} label="Conta" placeholder="Informe a Conta" onChange={changeConta} />
        </Col>
      </Row>
      <Row>
        <Col xl="3" md="6">
          <Card className="card-box border-0 card-shadow-first p-4 mb-5">
            <div className="d-flex align-items-center">
              <div className="d-40 rounded-circle bg-first text-white d-flex align-items-center justify-content-center font-size-lg mr-3">
                <FontAwesomeIcon icon={['fas', 'hand-holding-usd']} />
              </div>
              <div className="text-black-50">Saldo Anterior</div>
            </div>
            <div className="display-3 text-center line-height-sm text-black-50 text-center d-flex align-items-center pt-3 justify-content-center">
              <div>{blockingAreaGraph ? <Skeleton width={280} height={45} /> : <>R$ {formatSaldo(saldo.saldoAnterior)}</>}</div>
            </div>
          </Card>
        </Col>
        <Col xl="3" md="6">
          <Card className="card-box border-0 card-shadow-danger p-4 mb-5">
            <div className="d-flex align-items-center">
              <div className="d-40 rounded-circle bg-danger text-white d-flex align-items-center justify-content-center font-size-lg mr-3">
                <FontAwesomeIcon icon={['far', 'money-bill-alt']} />
              </div>
              <div className="text-black-50">Total de Débito</div>
            </div>
            <div className="display-3 text-center line-height-sm text-second text-center d-flex align-items-center pt-3 justify-content-center">
              <div>{blockingAreaGraph ? <Skeleton width={280} height={45} /> : <>R$ {decimalMaskFormatter(saldo.totalDebito)}</>}</div>
            </div>
          </Card>
        </Col>
        <Col xl="3" md="6">
          <Card className="card-box border-0 card-shadow-success p-4 mb-5">
            <div className="d-flex align-items-center">
              <div className="d-40 rounded-circle bg-success text-white d-flex align-items-center justify-content-center font-size-lg mr-3">
                <FontAwesomeIcon icon={['fas', 'coins']} />
              </div>
              <div className="text-black-50">Total de Crédito</div>
            </div>
            <div className="display-3 text-center line-height-sm text-second text-center d-flex align-items-center pt-3 justify-content-center">
              <div>{blockingAreaGraph ? <Skeleton width={280} height={45} /> : <>R$ {decimalMaskFormatter(saldo.totalCredito)}</>}</div>
            </div>
          </Card>
        </Col>
        <Col xl="3" md="6">
          <Card className="card-box border-0 card-shadow-primary p-4 mb-5">
            <div className="d-flex align-items-center">
              <div className="d-40 rounded-circle bg-primary text-white d-flex align-items-center justify-content-center font-size-lg mr-3">
                <FontAwesomeIcon icon={['fas', 'funnel-dollar']} />
              </div>
              <div className="text-black-50">Saldo Atual</div>
            </div>
            <div className="display-3 text-center line-height-sm text-black-50 text-center d-flex align-items-center pt-3 justify-content-center">
              <div>{blockingAreaGraph ? <Skeleton width={280} height={45} /> : <>R$ {formatSaldo(saldo.saldoAtual)}</>}</div>
            </div>
          </Card>
        </Col>
      </Row>

      <Row>
        <Col>
          <WrapperSimple sectionHeading="Evolução Contábil">
            <BlockUi blocking={blockingLineGraph} loader={<ScaleLoader color={'var(--success)'} loading={blockingLineGraph} />}>
              <Chart options={lineOptions} series={evolucaoContabil} type="line" height={400} />
            </BlockUi>
          </WrapperSimple>
        </Col>
      </Row>

      <Row>
        <Col>
          <WrapperSimple sectionHeading="Lançamentos Diários">
            <BlockUi blocking={blockingAreaGraph} loader={<ScaleLoader color={'var(--success)'} loading={blockingAreaGraph} />}>
              <Chart options={areaOptions} series={areaGraph} type="area" height={400} />
            </BlockUi>
          </WrapperSimple>
        </Col>
      </Row>
    </>
  );
};

export default DashboardMCG;
